import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { user } from './user.interface';

@Injectable({
  providedIn: 'root'
})
export class DataService {

  apiUrl = 'https://geektrust.s3-ap-southeast-1.amazonaws.com/adminui-problem/members.json';
  constructor(private http: HttpClient) { }

  getData() {
    return this.http.get<user[]>(this.apiUrl);
  }

  binarySearch(users: user[], left: number, right: number, subject: number): number {
    subject = subject * 1;
    if (right >= left) {
      let mid = Math.floor(left + ((right - left) / 2));

      if ((users[mid].id * 1) == subject)
        return mid;

      if ((users[mid].id * 1) > subject)
        return this.binarySearch(users, left, mid - 1, subject);

      return this.binarySearch(users, mid + 1, right, subject);
    }
    // not found
    return -1;
  }
}
