import {
  Component,
  OnChanges,
  OnInit,
  SimpleChanges,
  ElementRef,
  ViewChild,
} from "@angular/core";
import { MatTable, MatTableModule } from "@angular/material/table";
import { DataService } from "./data.service";
import { user } from "./user.interface";
import { Observable } from "rxjs";
import { SearchPipe } from "./search.pipe";
import { MatIconRegistry } from "@angular/material";
@Component({
  selector: "app-root",
  templateUrl: "./app.component.html",
  styleUrls: ["./app.component.scss"],
})
export class AppComponent implements OnInit {
  title = "admin-ui";
  data: user[];
  displayedColumns: string[];
  dataSource: user[];

  placeholderText = "search by name, email or role";
  searchText: string;
  userPlaceHolder: user;
  allSelected: boolean = false;
  //pagination
  totalItem = 0;
  perPage = 10;
  windowSize = 5;
  noArray: number[];
  currentPage = 1;
  startIndex = 0;
  endIndex = 9;

  constructor(private dataservice: DataService, private pipe: SearchPipe) { }

  ngOnInit() {
    this.dataservice.getData().subscribe((response) => {
      this.data = response;
      this.dataSource = [...response]; // copy of user array
      this.displayedColumns = ["select", "name", "email", "role", "action"];
      this.totalItem = response.length;
      this.paginate(this.currentPage);
    });
  }

  searchChange(value): void {
    this.paginate(this.currentPage);
  }

  paginate(currentPage = 1) {
    this.dataSource = this.pipe.transform(this.data, this.searchText);
    this.totalItem = this.dataSource.length;

    let totalPage = Math.ceil(this.totalItem / this.perPage);

    currentPage = currentPage < 1 ? 1 : currentPage;
    currentPage = currentPage > totalPage ? totalPage : currentPage;
    this.currentPage = currentPage;

    let noArray = [];
    for (let i = 0; i < totalPage; i++) {
      noArray.push(i);
    }
    this.noArray = noArray;

    this.startIndex = (currentPage - 1) * this.perPage;
    this.endIndex =
      currentPage * this.perPage - 1 < this.totalItem
        ? currentPage * this.perPage - 1
        : this.totalItem - 1;
    this.dataSource = this.dataSource.slice(this.startIndex, this.endIndex + 1);
    this.updateSelected();
  }
  setAllSelected(checked: boolean) {
    this.allSelected = checked;
    this.dataSource.forEach((user) => {
      user.isSelected = checked;
    });
  }
  someSelected() {
    let ret = false;
    if (!this.allSelected)
      this.dataSource.forEach((user) => {
        if (user.isSelected) {
          ret = true;
          return;
        }
      });
    return ret;
  }
  updateSelected() {
    let ret = true;
    this.dataSource.forEach((user) => {
      ret = ret && user.isSelected;
    });
    this.allSelected = ret;
  }
  update() {
    if (this.userPlaceHolder) {
      let id = this.getUser(this.userPlaceHolder.id);
      this.userPlaceHolder.isEditOn = false;
      this.data[id] = this.userPlaceHolder;
    }
    this.userPlaceHolder = null;
    this.paginate(this.currentPage);
  }
  edit(user: user) {
    if (this.userPlaceHolder) return false;
    this.userPlaceHolder = { ...user };
    return true;
  }

  deleteSelection() {
    this.dataSource.forEach((user) => {
      if (user.isSelected) this.delete(user.id);
    });
  }
  delete(id) {
    let index = this.getUser(id);
    this.data.splice(index, 1);
    this.paginate(this.currentPage);
  }
  getUser(id: number): number {
    //ensuring id is number
    id = 1 * id;
    let ret = this.dataservice.binarySearch(
      this.data,
      0,
      this.data.length - 1,
      id
    );
    return ret;
  }
}
